import "./styles.css";
import { Select, MenuItem } from "@material-ui/core";
import { useEffect, useState } from "react";
import FTable from "./FTable";
import Moment from "moment";
export default function App() {
  var today = new Date(),
    date =
      today.getDate() +
      "-" +
      (today.getMonth() + 1) +
      "-" +
      today.getFullYear();
  const [expand, setExpand] = useState(false);
  const [states, setStates] = useState([]);
  const [stCode, setStCode] = useState(["No State Selected"]);
  const [districts, setDistricts] = useState([]);
  const [distCode, setDistCode] = useState(["No District Selected"]);
  const [tableData, setTableData] = useState([]);
  const [newdate, setnewDate] = useState(date);
  // const [statesName, setStatesName] = useState(" ");
  // const [distName, setDistName] = useState(" ");
  useEffect(() => {
    const getStatesData = async () => {
      fetch("https://cdn-api.co-vin.in/api/v2/admin/location/states")
        .then((response) => response.json())
        .then((data) => {
          setStates(data.states);
        });
    };
    getStatesData();
  }, []);

  //For Districts
  useEffect(() => {
    fetch(`https://cdn-api.co-vin.in/api/v2/admin/location/districts/${stCode}`)
      .then((response) => response.json())
      .then((data) => {
        setDistricts(data.districts);
      });
  }, [stCode]);
  //tableData
  useEffect(() => {
    const getStatesData = async () => {
      fetch(
        `https://cdn-api.co-vin.in/api/v2/appointment/sessions/public/findByDistrict?district_id=${distCode}&date=${newdate}`
      )
        .then((response) => response.json())
        .then((data) => {
          setTableData(data.sessions);
        });
    };
    getStatesData();
  }, [distCode, newdate]);
  const onstChange = async (event) => {
    setStCode(event.target.value);
    setExpand(true);
  };
  const ondistChange = async (event) => {
    setDistCode(event.target.value);
  };
  const dtChange = async (event) => {
    setnewDate(Moment(event.target.value).format("DD-MM-YYYY"));
  };
  return (
    <>
      <h1>
        <span style={{ color: "green" }}>Search Vaccination Center</span>
      </h1>
      <div class="container">
        <div class="row">
          <div class="col-sm bg-info">
            <Select
              labelId="demo-simple-select-label"
              id="demo-simple-select"
              value={stCode}
              onChange={onstChange}
            >
              <MenuItem value="No State Selected">Select State</MenuItem>
              {Object.keys(states).map((state, key) => (
                <MenuItem key={key} value={states[state].state_id}>
                  {states[state].state_name}
                </MenuItem>
              ))}
            </Select>
          </div>
          <div class="col-sm text-white bg-info">
            {expand ? (
              <Select
                labelId="demo-simple-select-label"
                id="demo-simple-select"
                value={distCode}
                onChange={ondistChange}
              >
                <MenuItem value="No District Selected">
                  Select District
                </MenuItem>
                {Object.keys(districts).map((dist, key) => (
                  <MenuItem key={key} value={districts[dist].district_id}>
                    {districts[dist].district_name}
                  </MenuItem>
                ))}
              </Select>
            ) : null}
          </div>
          <br />
          <div class="col-sm text-light bg-info">
            <lebel>Choose Date</lebel>
            <input
              type="date"
              value={newdate}
              onChange={dtChange}
              placeholder="Select Date"
              style={{ width: "auto" }}
            />
          </div>
        </div>
      </div>
      {/* <h1>
        <span style={{ color: "red" }}>{stCode}</span>
      </h1>
      <h1>
        <span style={{ color: "green" }}>{distCode}</span>
      </h1>
      <h1>
        <span style={{ color: "green" }}>{newdate}</span>
      </h1> */}
      <h1>
        <span style={{ color: "Orange" }}>Vaccination Available</span>
      </h1>
      {/* <Table tableData={tableData} /> */}
      <FTable tableData={tableData} />
    </>
  );
}
