import React from "react";
//import "./Table.css";
function FTable({ tableData }) {
  //console.log(tableData);

  return (
    <>
      <table class="table table-striped">
        <thead>
          <tr class="table-primary">
            <th scope="col">Center Name</th>
            <th scope="col">Fee type</th>
            <th scope="col">Vaccine</th>
            <th scope="col">Slot Available</th>
            <th scope="col">Min Age Limit</th>
            <th scope="col">Timing</th>
          </tr>
        </thead>
        <tbody>
          {tableData
            ? Object.keys(tableData).map((tdata) => (
                <tr>
                  <td>{tableData[tdata].name}</td>
                  <td>
                    <strong>{tableData[tdata].fee_type}</strong>
                  </td>
                  <td>{tableData[tdata].vaccine}</td>
                  <td>
                    <strong>{tableData[tdata].available_capacity}</strong>
                  </td>
                  <td>{tableData[tdata].min_age_limit}</td>
                  <td>
                    <strong>
                      {tableData[tdata].from} to {tableData[tdata].to}
                    </strong>
                  </td>
                </tr>
              ))
            : null}
        </tbody>
      </table>
    </>
  );
}
export default FTable;
